import React, { useContext } from "react";
import { GlobalContext } from "../contexts/GlobalContext";

const IncomeExpense = () => {
  const { transactions } = useContext(GlobalContext);
  const amounts = transactions.map((transaction) => transaction.amount);

  // income
  const incomeList =
    amounts.length > 0 ? amounts.filter((item) => item > 0) : 0;
  const income =
    incomeList.length > 0 ? incomeList.reduce((acc, item) => (acc += item)) : 0;

  // expenses
  const expenseList =
    amounts.length > 0 ? amounts.filter((item) => item < 0) : 0;
  const expense =
    expenseList.length > 0
      ? expenseList.reduce((acc, item) => (acc += item))
      : 0;

  return (
    <div className="tracker__inc__exp">
      <div className="tracker__income">
        <h4>Income</h4>
        <span>
          <i className="fas fa-rupee-sign"></i> {income}
        </span>
      </div>
      <div className="tracker__expense">
        <h4>Expense</h4>
        <span>
          {" "}
          <i className="fas fa-rupee-sign"></i> {expense}
        </span>
      </div>
    </div>
  );
};

export default IncomeExpense;
